@import url(https://fonts.googleapis.com/css2?family=IBM+Plex+Mono:wght@500&display=swap);

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* PrismJS 1.24.1
https://prismjs.com/download.html#themes=prism-twilight&languages=markup+css+clike+javascript */


/**
 * prism.js Twilight theme
 * Based (more or less) on the Twilight theme originally of Textmate fame.
 * @author Remy Bach
 */

code[class*="language-"],
pre[class*="language-"] {
    color: white;
    background: none;
    font-family: Consolas, Monaco, 'Andale Mono', 'Ubuntu Mono', monospace;
    font-size: 0.9em;
    text-align: left;
    text-shadow: 0 -.1em .2em black;
    white-space: pre-wrap;
    word-spacing: normal;
    word-break: normal;
    word-wrap: normal;
    line-height: 1.5;
    -moz-tab-size: 4;
    tab-size: 4;
    -webkit-hyphens: none;
    -ms-hyphens: none;
    hyphens: none;
}

pre[class*="language-"],
:not(pre)>code[class*="language-"] {
    background: hsl(0, 0%, 8%);
    /* #141414 */
}


/* Code blocks */

pre[class*="language-"] {
    border-radius: .5em;
    border: .3em solid hsl(0, 0%, 33%);
    /* #282A2B */
    box-shadow: 1px 1px .5em black inset;
    margin: .5em 0;
    overflow: auto;
    padding: 1em;
}

pre[class*="language-"]::selection {
    /* Safari */
    background: hsl(200, 4%, 16%);
    /* #282A2B */
}


/* Text Selection colour */

pre[class*="language-"]::selection,
pre[class*="language-"] ::selection,
code[class*="language-"]::selection,
code[class*="language-"] ::selection {
    text-shadow: none;
    background: hsla(0, 0%, 93%, 0.15);
    /* #EDEDED */
}


/* Inline code */

:not(pre)>code[class*="language-"] {
    border-radius: .3em;
    border: .13em solid hsl(0, 0%, 33%);
    /* #545454 */
    box-shadow: 1px 1px .3em -.1em black inset;
    padding: .15em .2em .05em;
    white-space: normal;
}

.token.comment,
.token.prolog,
.token.doctype,
.token.cdata {
    color: hsl(0, 0%, 47%);
    /* #777777 */
}

.token.punctuation {
    opacity: .7;
}

.token.namespace {
    opacity: .7;
}

.token.tag,
.token.boolean,
.token.number,
.token.deleted {
    color: hsl(14, 58%, 55%);
    /* #CF6A4C */
}

.token.keyword,
.token.property,
.token.selector,
.token.constant,
.token.symbol,
.token.builtin {
    color: hsl(53, 89%, 79%);
    /* #F9EE98 */
}

.token.attr-name,
.token.attr-value,
.token.string,
.token.char,
.token.operator,
.token.entity,
.token.url,
.language-css .token.string,
.style .token.string,
.token.variable,
.token.inserted {
    color: hsl(44, 100%, 65%);
    /* #FFCF4D */
}

.token.atrule {
    color: hsl(218, 22%, 55%);
    /* #7587A6 */
}

.token.regex,
.token.important {
    color: hsl(42, 75%, 65%);
    /* #E9C062 */
}

.token.important,
.token.bold {
    font-weight: bold;
}

.token.italic {
    font-style: italic;
}

.token.entity {
    cursor: help;
}

pre[data-line] {
    padding: 1em 0 1em 3em;
    position: relative;
}


/* Markup */

.language-markup .token.tag,
.language-markup .token.attr-name,
.language-markup .token.punctuation {
    color: hsl(33, 33%, 52%);
    /* #AC885B */
}


/* Make the tokens sit above the line highlight so the colours don't look faded. */

.token {
    position: relative;
    z-index: 1;
}

.line-highlight {
    background: hsla(0, 0%, 33%, 0.25);
    /* #545454 */
    background: linear-gradient(to right, hsla(0, 0%, 33%, .1) 70%, hsla(0, 0%, 33%, 0));
    /* #545454 */
    border-bottom: 1px dashed hsl(0, 0%, 33%);
    /* #545454 */
    border-top: 1px dashed hsl(0, 0%, 33%);
    /* #545454 */
    left: 0;
    line-height: inherit;
    margin-top: 0.75em;
    /* Same as .prismâ€™s padding-top */
    padding: inherit 0;
    pointer-events: none;
    position: absolute;
    right: 0;
    white-space: pre;
    z-index: 0;
}

.line-highlight:before,
.line-highlight[data-end]:after {
    background-color: hsl(215, 15%, 59%);
    /* #8794A6 */
    border-radius: 999px;
    box-shadow: 0 1px white;
    color: hsl(24, 20%, 95%);
    /* #F5F2F0 */
    content: attr(data-start);
    font: bold 65%/1.5 sans-serif;
    left: .6em;
    min-width: 1em;
    padding: 0 .5em;
    position: absolute;
    text-align: center;
    text-shadow: none;
    top: .4em;
    vertical-align: .3em;
}

.line-highlight[data-end]:after {
    bottom: .4em;
    content: attr(data-end);
    top: auto;
}
* {
    padding: 0;
    margin: 0;
}

body {
    overflow-x: hidden;
}

h1 {
    font-family: 'IBM Plex Mono', monospace;
    color: #FFFFFF;
}

h1:hover, h1:visited {
    font-family: 'IBM Plex Mono', monospace;
    color: #FFC629;
}

a {
    text-decoration: none;
}

.App {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
    min-height: 100vh;
    background-color: #0e0d0c;
}

main {
    position: relative;
    background-color: #0e0d0c;
    background-image: url(https://iili.io/RPrgLX.jpg);
    background-position: 100% 0%;
    background-attachment: fixed;
    background-size: 100%;
    background-repeat: no-repeat;  
    width: 100%;
    min-height: 100vh;
    display: flex;
    align-items: flex-start;
    justify-content: space-evenly;
    font-size: calc(10px + 2vmin);
    color: white;
}

.box {
    z-index: 2;
    position: sticky;
    top: 0;
    padding: 40px 20px;
}

.console {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.consolelog {
    width: 100%;
    box-sizing: border-box;
    background-color: #2d2d2d;
    border: 1px solid #e9dbdb;
    border-radius: 5px;
    padding: 10px;
    min-height: 100px;
    max-height: 300px;
    overflow: auto;
}

.box h6 {
    position: absolute;
    top: 1px;
    left: 75px;
    opacity: 0.7;
}

.imagem {
    margin-top: 145px;
    width: 100%;
}

.categories {
    box-sizing: border-box;
    padding: 10px;
    margin: 10px 0;
    color: white;
    background-color: #2d2d2d;
    font-size: 18px;
}

.snippets {
    max-width: 50%;
    padding: 30px;
    display: flex;
    flex-direction: column;
}

.btn-search {
    box-sizing: border-box;
    padding: 10px;
    color: white;
    background-color: #2d2d2d;
    font-size: 18px;
}

#code {
    width: 97%;
    padding: 15px;
    margin: 0 15px;
    padding: 10px;
    color: white;
    background-color: rgba(105, 105, 105, 0.218);
    border: 1px solid #FFC629;
    border-radius: 15px;
    transition: all 0.5s ease;
    font-size: 20px;
}

#code:hover, #code:active {
    background-color: #0e0d0c;
}

pre {
    position: relative;
    transition: all 1s ease;
}

pre:hover {
    box-shadow: 0 0 60px 20px #FFC629, 0 0 100px 40px rgba(255, 0, 255, 0.1), 0 0 100px 90px rgba(0, 255, 255, 0.1) !important;
}

.controls{
    position: absolute;
    top: -5px;
    right: 0;
}

pre button {
    color: #FFC629;
    padding: 2px;
    margin-right: 5px;
    background-color: rgba(105, 105, 105, 0.218);
    border: 1px solid #FFC629;
    border-radius: 5px;
    transition: all 0.5s ease;
}

pre button:hover {
    color: rgba(255, 255, 255, 1);
    background-color: rgba(105, 105, 105, 0.218);
}

.App-link {
    color: #FFC629;
}

.btn-executar {
    margin: 15px;
    padding: 10px;
    color: #FFC629;
    background-color: rgba(105, 105, 105, 0.218);
    border: 1px solid #FFC629;
    border-radius: 5px;
    transition: all 0.5s ease;
    font-size: 20px;
}

.btn-executar:hover {
    color: white;
}

@-webkit-keyframes App-logo-spin {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

@keyframes App-logo-spin {
    from {
        -webkit-transform: rotate(0deg);
                transform: rotate(0deg);
    }
    to {
        -webkit-transform: rotate(360deg);
                transform: rotate(360deg);
    }
}

.fade-enter {
    opacity: 0.01;
}

.fade-enter.fade-enter-active {
    opacity: 1;
    transition: all 0.5s ease;
}

.fade-exit {
    opacity: 1;
}

.fade-exit.fade-exit-active {
    opacity: 0.01;
    transition: all 0.5s ease;
}
